import React, { FC, useEffect } from 'react';
import classnames from 'classnames/bind';
import { Helmet } from 'react-helmet';
import { useSelector, useDispatch } from 'react-redux';

import TopLoading from 'components/TopLoading';
import Container from 'components/Container';
import SectionTitle from 'components/SectionTitle';
import {
  getLibraryInfo,
  getLibraryInfoLoading,
  getLibrarySectionLoading,
  getLibraryLinkLoading,
} from 'store/library/selectors';
import { libraryPending } from 'store/library/actions';
import { LibraryLinkType } from 'types/common';
import styles from './styles.module.scss';

const cx = classnames.bind(styles);

const Library: FC = () => {
  const dispatch = useDispatch();

  const {
    sections,
    links,
  } = useSelector(getLibraryInfo);

  const isLoading = useSelector(getLibraryInfoLoading);
  const sectionIsLoading = useSelector(getLibrarySectionLoading);
  const linkIsLoading = useSelector(getLibraryLinkLoading);

  useEffect(() => {
    dispatch(libraryPending());
  }, [dispatch]);

  const linksBySection: { [key: number]: LibraryLinkType[] } = {};

  links.forEach((link) => {
    if (!linksBySection[link.section_id]) {
      linksBySection[link.section_id] = [];
    }

    linksBySection[link.section_id].push(link);
  });

  return (
    <>
      <Helmet>
        <title>Novakid - Library</title>
      </Helmet>
      <TopLoading loading={isLoading} />
      <div className={cx('library')}>
        <Container>
          {sections.map((section) => (
            <div className={cx('section')}>
              <SectionTitle
                title={section.name_en}
                bold
                loading={sectionIsLoading || linkIsLoading}
              />
              <div className={cx('links')}>
                {linksBySection[section.id] && (
                  linksBySection[section.id].map((link) => (
                    <a href={link.link} target="_blank" rel="noopener noreferrer">
                      <img
                        src={`${process.env.REACT_APP_API_URL || ''}/files/${link.image_file_id}/content`}
                      />
                      <span>{link.name_en}</span>
                    </a>
                  ))
                )}
              </div>
            </div>
          ))}
          {!isLoading && !sections.length && (
            <div>Library is empty</div>
          )}
        </Container>
      </div>
    </>
  );
};

export default Library;
