import { AppStateType } from 'types';
import LibraryState from 'types/libraryType';

export const getLibraryInfoLoading = (state: AppStateType): boolean => (
  state.library.sectionLoading
  || state.library.linkLoading
  || state.library.topLoading
);

export const getLibrarySectionLoading = (state: AppStateType): boolean => (
  state.library.sectionLoading
);

export const getLibraryLinkLoading = (state: AppStateType): boolean => (
  state.library.linkLoading
);

export const getLibraryInfo = (state: AppStateType): LibraryState => state.library;
