import React, { FC, memo } from 'react';
import classnames from 'classnames/bind';
import BounceLoading from 'components/BounceLoading';

import styles from './styles.module.scss';

const cx = classnames.bind(styles);

interface PropsType {
  title: string;
  loading?: boolean;
  bold?: boolean;
}

const SectionTitle: FC<PropsType> = (props) => {
  const {
    title,
    loading,
    bold,
  } = props;

  const hasLoading = typeof loading !== 'undefined';

  return (
    <blockquote className={cx('sectionTitle', { bold })}>
      <p className={cx('title')}>{title}</p>
      {hasLoading && (
        <div className={cx('loader')}>
          <BounceLoading loading={!!loading} contains />
        </div>
      )}
    </blockquote>
  );
};

export default memo(SectionTitle);
